<template>
  <div class="page-container">
    <div class="game-container">
      <!-- Cinematic Opening -->
      <div v-if="gameStage === 'intro'" class="intro-section">
        <h1>What if {{ selectedPartyName }} comes to power?</h1>
        <p>
          The year is 2030. {{ selectedPartyName }} has taken complete control and
          starts transforming the Netherlands. What will the country look like?
          You decide the course we take.
        </p>
        <button @click="proceedToStage('priorities')">Start the Simulation</button>
      </div>

      <!-- Theme Priorities -->
      <div v-if="gameStage === 'priorities'" class="priorities-section">
        <h2>Set Your Priorities</h2>
        <p>
          Drag the sliders to indicate how important each theme is to you. These
          choices will directly impact your simulation.
        </p>
        <div v-for="theme in themes" :key="theme.id" class="theme-priority">
          <label>{{ theme.name }}</label>
          <input
              type="range"
              v-model="theme.priority"
              min="1"
              max="10"
              step="1"
          />
          <span>{{ theme.priority }}</span>
        </div>
        <button @click="proceedToStage('simulation')">Start Simulation</button>
      </div>

      <!-- Simulation Story -->
      <div v-if="gameStage === 'simulation'" class="simulation-section">
        <h2>{{ story.title }}</h2>
        <p>{{ story.content }}</p>

        <div class="story-options">
          <button
              v-for="choice in story.choices"
              :key="choice.id"
              @click="makeChoice(choice)"
          >
            {{ choice.text }}
          </button>
        </div>
      </div>

      <!-- Ending Results -->
      <div v-if="gameStage === 'ending'" class="ending-section">
        <h1>Final Outcome: {{ ending.title }}</h1>
        <p>{{ ending.description }}</p>
        <img :src="ending.image" alt="Ending visualization" />
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "vue";

export default {
  name: "PoliticalSimulationGame",
  setup() {
    // Initial Data
    const selectedParty = ref("groenlinks"); // Example starting party
    const gameStage = ref("intro"); // Stages: intro -> priorities -> simulation -> ending
    const themes = ref([
      { id: "climate", name: "Climate & Environment", priority: 5 },
      { id: "economy", name: "Economy", priority: 5 },
      { id: "education", name: "Education", priority: 5 },
      { id: "healthcare", name: "Healthcare", priority: 5 },
    ]);

    // Story Elements
    const stories = ref({
      groenlinks: [
        {
          id: 1,
          title: "The Green Offensive",
          content:
              "GroenLinks' policies have drastically changed the Netherlands: thousands of new wind turbines dot the landscape, a meat tax has been introduced, and there are massive investments in public transport.",
          choices: [
            {
              id: "support_policy",
              text: "Support the policies and enforce stricter regulations.",
            },
            {
              id: "balance_policy",
              text: "Seek a compromise with other parties.",
            },
            {
              id: "reverse_policy",
              text: "Grant farmers and businesses more freedom to adapt.",
            },
          ],
        },
        {
          id: 2,
          title: "Protests in The Hague",
          content:
              "Stricter environmental rules have led to unrest: farmers and businesses are staging mass protests in The Hague. Meanwhile, support for change is growing among youth.",
          choices: [
            { id: "crackdown", text: "Implement harsh measures against protests." },
            { id: "negotiation", text: "Negotiate with the demonstrators." },
            { id: "rollback", text: "Revoke certain measures." },
          ],
        },
      ],
    });

    const endings = ref({
      groenlinks: {
        utopia: {
          title: "Green Utopia",
          description:
              "The Netherlands has become a green paradise with 100% sustainable energy and a happy population.",
          image: "/images/green-utopia.png",
        },
        dystopia: {
          title: "Chaos in the Netherlands",
          description:
              "Strict regulations have led to a social crisis with a declining economy and mass protests.",
          image: "/images/dystopia.png",
        },
      },
    });

    const currentStoryIndex = ref(0);

    // Computed Values
    const selectedPartyName = computed(() => {
      const partyMap = {
        groenlinks: "GroenLinks",
        vvd: "VVD",
        pvda: "PvdA",
      };
      return partyMap[selectedParty.value] || "Unknown";
    });

    const story = computed(() =>
        stories.value[selectedParty.value][currentStoryIndex.value]
    );

    const ending = computed(() => {
      const totalPriority = themes.value.reduce(
          (sum, theme) => sum + theme.priority,
          0
      );
      return totalPriority > 30
          ? endings.value[selectedParty.value].utopia
          : endings.value[selectedParty.value].dystopia;
    });

    // Functions
    const proceedToStage = (stage) => {
      gameStage.value = stage;
    };

    const makeChoice = () => {
      if (currentStoryIndex.value < stories.value[selectedParty.value].length - 1) {
        currentStoryIndex.value++;
      } else {
        proceedToStage("ending");
      }
    };

    return {
      gameStage,
      selectedPartyName,
      themes,
      proceedToStage,
      story,
      makeChoice,
      ending,
    };
  },
};
</script>

