<template>
  <div class="parent">
    <div id="nav"><h1>Admin</h1>
      <div class="profileBtn" v-if="isLoggedIn()">
        <div class="dropdown">
          <button class="dropbtn">Profile</button>
          <div class="dropdown-content"><a @click="$router.push('/profile')">Profile</a> <a v-if="isAdmin()"
                                                                                            @click="$router.push('/admin')">Admin</a>
            <a @click="logOut()">Logout</a></div>
        </div>
      </div>
    </div>
    <div id="body">
      <div class="header-container"><h1>Userlist</h1>        <input v-model="searchTerm" type="text"
                                                                    placeholder="Search users by email..."
                                                                    class="search-input"/></div>
      <div class="table__body">
        <table>
          <thead>
          <tr>
            <th> Id</th>
            <th> Name</th>
            <th> Email</th>
            <th> Account status</th>
            <th> role</th>

          </tr>
          </thead>
          <tbody>
          <tr v-for="user in filteredUsers" :key="user.id">
            <td>{{ user.id }}</td>
            <td>{{ user.username }}</td>
            <td>{{ user.email }}</td>
            <td>
              <button :id="'button-' + user.id" :class="{'active-btn': user.active, 'inactive-btn': !user.active}"
                      @click="toggleUserStatus(user.id, user.active)">
                {{ user.active ? 'Active' : 'Inactive' }}
              </button>
              <button @click="deleteUser(user.id)" class="delete-btn">Delete</button>
            </td>
            <td>
              <button :id="'button-' + user.id" :class="{'active-btn': user.admin, 'inactive-btn': !user.admin}"
                      @click="updateUserRole(user.id, user.admin)">
                {{ user.admin ? 'Admin' : 'User' }}
              </button>
            </td>
          </tr>
          <tr v-if="filteredUsers.length === 0">
            <td colspan="5" style="text-align: center;">No users found</td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>

</template>
<script>
import axios from 'axios';
import {API_URL} from '@/config';

export default {
  name: 'AdminPage',
  data() {
    return {
      users: [],
      searchTerm: '',
    };
  },
  computed: {
    filteredUsers() {
      return this.users.filter(user =>
          user.email.toLowerCase().includes(this.searchTerm.toLowerCase())
      );
    },
  },
  methods: {
    isLoggedIn() {
      return !!localStorage.getItem('token');
    },
    isAdmin() {
      return !!localStorage.getItem('isAdmin');
    },
    logOut() {
      localStorage.removeItem('token');
      localStorage.removeItem('isAdmin');
      this.$router.push('/');
    },
    async fetchUsers() {
      try {
        const token = localStorage.getItem('token');
        const response = await axios.get(`${API_URL}/admin/users`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
        this.users = response.data;
      } catch (error) {
        if (error.response && error.response.status === 401) {
          this.logOut(); // Log out and redirect to login if unauthorized
          window.alert('Unauthorized access, please log in again.');
        } else {
          console.error('Error fetching users:', error);
        }
      }
    },
    async toggleUserStatus(userId, isActive) {
      const token = localStorage.getItem('token');
      try {
        await axios.put(`${API_URL}/admin/user/${userId}/status`, null, {
          params: { isActive: !isActive },
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`,
          },

        });
        const user = this.users.find(u => u.id === userId);
        if (user) {
          user.active = !isActive;
        }
      } catch (error) {
        if (error.response && error.response.status === 403) {
          window.alert('You cannot deactivate your own account.');
        } else if (error.response && error.response.status === 401) {
          this.logOut();
          window.alert('Unauthorized access, please log in again.');
        } else {
          console.error('Error updating user status:', error);
        }
      }
    },
    async deleteUser(id) { // Changed from userId to username
      const token = localStorage.getItem('token');
      try {
        await axios.delete(`${API_URL}/admin/user/${id}`, { // Updated to use username
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
        // Remove the user from the list after deletion
        // Also use username here
      } catch (error) {
        console.error('Error deleting user:', error);
        window.alert('An error occurred while trying to delete the user.');
      }
    },
    async updateUserRole(userId, isAdmin) {
      const token = localStorage.getItem('token');
      try {
        await axios.put(`${API_URL}/admin/user/${userId}/role`, null, {
          params: {isAdmin: !isAdmin},
          headers: {
            'Authorization': `Bearer ${token}`
          },

        });
        const user = this.users.find(u => u.id === userId);
        if (user) {
          console.log(user.admin)
          user.role = !isAdmin;
        }
      } catch (error) {
        console.error('Error updating user role:', error);
        window.alert('An error occurred while trying to update the user role.');
      }
    }


  },
  mounted() {
    if (!this.isLoggedIn()) {
      this.$router.push('/');
      window.alert('Please log in to access the admin page.');
    } else {
      this.fetchUsers();
    }
  },
};
</script>


