<template>
  <div class="community-chat">
    <div class="chat-header">
      <h2>Community Chat App</h2>
      <button class="leave-button" @click="leaveCommunity">Leave</button>
    </div>

    <div class="new-message-box">
      <textarea v-model="newMessage" placeholder="Type message here..."></textarea>
      <input type="file" @change="handleFileChange" accept="image/*" />
      <button class="post-button" @click="postMessage">Post</button>
    </div>

    <div class="posts-container">
      <div v-for="message in chatMessages" :key="message.id" class="message-box">
        <div class="user-info">
          <span class="user-avatar"></span>
          <span class="username">{{ message.username }}</span>
        </div>
        <img v-if="message.image" :src="message.image" alt="Post Image" class="message-image" />
        <p class="message-area">{{ message.text }}</p>

        <div class="message-actions">
          <button @click="toggleComments(message.id)">
            {{ isCommentsOpen(message.id) ? "Hide" : "Show" }} Comments
          </button>
          <button @click="likeMessage(message.id)">
            Like
          </button>
          <span>{{ message.likeCount }} Likes</span> <!-- Weergave van het aantal likes -->
          <button @click="shareMessage(message.id)">Share</button>
        </div>


        <div v-if="isCommentsOpen(message.id)" class="comments-section">
          <div v-for="comment in message.comments" :key="comment.id" class="comment">
            <span class="comment-username">{{ comment.username }}</span>
            <p class="comment-text">{{ comment.content }}</p>
          </div>
          <div class="new-comment-box">
            <textarea v-model="newComments" placeholder="Add a comment..." />
            <button @click="addComment(message.id)">Comment</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { API_URL } from "@/config";
import jwtDecode from "jwt-decode";

export default {
  name: "CommunityChatComponent",
  props: ["communityId"],
  data() {
    return {
      chatMessages: [],
      newMessage: "",
      selectedFile: null,
      openComments: {},
      newComments: "",
    };
  },
  methods: {
    async fetchPosts() {
      try {
        const token = localStorage.getItem("token");
        const response = await fetch(`${API_URL}/api/communities/${this.communityId}/posts`, {
          method: "GET",
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!response.ok) throw new Error("Failed to fetch posts");
        const posts = await response.json();

        this.chatMessages = posts.map((post) => ({
          id: post.id,
          text: post.content,
          username: post.user?.username || "Anonymous",
          image: post.image ? `data:image/jpeg;base64,${post.image}` : null,
          likeCount: post.likeCount || 0,
          comments: post.comments || [],
        }));
      } catch (error) {
        console.error("Error fetching posts:", error);
      }
    },

    async fetchComments(postId) {
      const token = localStorage.getItem("token");
      try {
        const response = await fetch(`${API_URL}/api/comments/${postId}/getComments`, {
          method: "GET",
          headers: { Authorization: `Bearer ${token}` },
        });

        if (!response.ok) throw new Error("Failed to fetch comments");
        const comments = await response.json();

        const postIndex = this.chatMessages.findIndex((msg) => msg.id === postId);
        if (postIndex !== -1) {
          this.chatMessages[postIndex].comments = comments;
        }
      } catch (error) {
        console.error("Error fetching comments:", error);
      }
    },

    async addComment(messageId) {
      const token = localStorage.getItem("token");
      if (!token) return alert("User not authenticated. Please log in.");

      try {
        const decodedToken = jwtDecode(token);
        const response = await fetch(`${API_URL}/api/comments/${messageId}/saveComments`, {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            content: this.newComments,
            userId: decodedToken.id,
          }),
        });

        if (!response.ok) throw new Error("Failed to add comment");
        this.newComments = "";
        await this.fetchComments(messageId);
      } catch (error) {
        console.error("Error adding comment:", error);
      }
    },

    async postMessage() {
      const token = localStorage.getItem("token");
      if (!token) return alert("User not authenticated. Please log in.");

      try {
        const decodedToken = jwtDecode(token);
        const formData = new FormData();
        formData.append("content", this.newMessage);
        formData.append("userid", decodedToken.id);
        if (this.selectedFile) formData.append("image", this.selectedFile);

        const response = await fetch(`${API_URL}/api/communities/${this.communityId}/posts/create`, {
          method: "POST",
          headers: { Authorization: `Bearer ${token}` },
          body: formData,
        });

        if (!response.ok) throw new Error("Failed to post message");

        this.newMessage = "";
        this.selectedFile = null;
        this.fetchPosts();
      } catch (error) {
        console.error("Error posting message:", error);
      }
    },

    handleFileChange(event) {
      const file = event.target.files[0];
      if (!file.type.startsWith("image/")) {
        return alert("Please upload a valid image file.");
      }
      if (file.size > 5 * 1024 * 1024) {
        return alert("File size exceeds 5MB. Please upload a smaller file.");
      }
      this.selectedFile = file;
    },
    async likeMessage(messageId) {
      const token = localStorage.getItem("token");
      if (!token) return alert("User not authenticated. Please log in.");

      try {
        // Decodeer de JWT-token om de userId te krijgen
        const decodedToken = jwtDecode(token);

        // Verstuur het verzoek naar de server met de userId in de query string
        const response = await fetch(`${API_URL}/api/communities/${this.communityId}/posts/${messageId}/like?userId=${decodedToken.id}`, {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!response.ok) throw new Error("Failed to like the post");

        alert("Post liked!");

        await this.fetchPosts();

        // Vind het bericht en verhoog het aantal likes
        const post = this.chatMessages.find(msg => msg.id === messageId);
        if (post) {
          post.likes += 1; // Verhoog het aantal likes met 1
        }
      } catch (error) {
        console.error("Error liking post:", error);
      }

    }

    ,

    toggleComments(messageId) {
      this.openComments[messageId] = !this.openComments[messageId];
      if (this.openComments[messageId]) {
        this.fetchComments(messageId);
      }
    },

    isCommentsOpen(messageId) {
      return !!this.openComments[messageId];
    },

    async leaveCommunity() {
      const token = localStorage.getItem("token");
      if (!token) return alert("User not authenticated. Please log in.");

      try {
        const decodedToken = jwtDecode(token);
        const response = await fetch(`${API_URL}/api/communities/${this.communityId}/leave?userId=${decodedToken.id}`, {
          method: "DELETE",
          headers: {Authorization: `Bearer ${token}`},
        });

        if (!response.ok) throw new Error("Failed to leave community");
        alert("You have left the community.");
        this.$router.push({name: "Communities"});
      } catch (error) {
        console.error("Error leaving community:", error);
      }
    },
  },
  mounted() {
    this.fetchPosts();
  },
};
</script>

